<template>
  <div>
    <div class="col-12 align-self-center">
      <h1 class="text-center" v-html="html(outputText)"></h1>
        <div class="row" v-on:enter="enter">
          <div class="col-12 align-self-center" v-if="show">
            <transition name="fade" class="m-2">
              <a href="https://www.linkedin.com/in/melovinpedida/" target="_blank"><fa icon="linkedin" type="fab" class="fa-2x"></fa></a>
            </transition>
            <transition name="fade" class="m-2">
              <a href="https://www.facebook.com/melovinpogi" target="_blank"><fa icon="facebook-square" type="fab" class="fa-2x"></fa></a>
            </transition>
            <transition name="fade" class="m-2">
              <a href="mailto:pedidamelovin@gmail.com?subject=inquiry-github" target="_blank"><fa icon="envelope-square" class="fa-2x"></fa></a>
            </transition>
          </div>
        </div>
    </div>
  </div>
</template>

<script>
const introMsg = `Hello, I'm Melovin Pedida. <br>
                  I'm a Software Developer.`;
export default {
  methods: {
    output() {
      if (this.ctr < this.msg.length) {
        this.outputText += this.msg.charAt(this.ctr);
        this.ctr++;
        setTimeout(this.output, 100);
      } else {
        this.fadeButton();
      }
    },
    html() {
      return this.outputText;
    },
    fadeButton() {
      this.show = !this.show;
    },
  },
  mounted() {
    this.output();
  },
  data() {
    return {
      msg: introMsg,
      outputText: "",
      ctr: 0,
      show: false,      
    };
  },
};
</script>

